.portfolio {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 70vh;
}

.p-swipe-cta{
  margin-top: 0;
  display: none;
}

/* heading */
.portfolio span:nth-of-type(1) {
  color: var(--black);
  font-size: 2rem;
  font-weight: bold;
}

.portfolio span:nth-of-type(2) {
  color: var(--main);
  font-size: 2.5rem;
  font-weight: bold;
}

/* slider */
.portfolio .swiper{
    overflow: visible!important;
}

.portfolio-slider {
  margin-top: 3rem;
  width: 100%;
  gap: 20rem;  
}

.portfolio-slider .swiper-slide {
  width: 20rem;
  margin-right: 5rem;
}
.portfolio img {
  width: 100%;
  gap: 5%;
  filter: drop-shadow(-12px 15px 13px rgba(232, 104, 183, 0.25));
  border-radius: 19px;
}


@media screen and (max-width: 480px){
  .portfolio{
    margin-bottom: 8rem;
  }
  .portfolio img {
      width: 100%;     
  }

  .p-swipe-cta{
    display: block;
  }
}

