.floatingDiv {
  justify-content:space-around;
  background: white;
  box-shadow: none;
  border-radius: 17px;
  display: flex;
  align-items: center;
  padding: 0px 28px 0px 0px;
  height: 3.5rem;
  
}
.floatingDiv > img {
  transform: scale(0.3);
}
.floatingDiv > span {
  font-family: "poppins";
  font-size: 16px;
  color: black;
}

